import React from 'react'
import { Tabs,Card,List} from 'antd';
import {  } from 'antd';

const { Meta } = Card;
const { TabPane } = Tabs;

export default function Content() {
    return (
        <div className="content_div">
             <div className="breadcrumb">
                <div style={{fontSize:20,fontWeight:600}}>业务范围</div>
                <div style={{margin:34}}>{`当前位置：网站首页 > 业务范围`}</div>
            </div>
             <div style={{maxWidth:1200,margin:'30px auto'}}>
             <Tabs defaultActiveKey="1" centered>
                <TabPane tab="质量管理认证" key="1">
                <List
                    grid={{ gutter: 16, column: 4 }}
                    dataSource={[1,2,3,4,5,6]}
                    renderItem={item => (
                    <List.Item>
                          <Card
                                hoverable
                                style={{ width: 240 }}
                                cover={<img alt="example" src="data:image/png;base64," />}
                            >
                                <Meta title="ISO9001" description="详细信息" />
                            </Card>
                    </List.Item>
                    )}
                />
                </TabPane>
                <TabPane tab="精益生产" key="2">
                <List
                    grid={{ gutter: 16, column: 4 }}
                    dataSource={[1,2,3,4,5,6]}
                    renderItem={item => (
                    <List.Item>
                          <Card
                                hoverable
                                style={{ width: 240 }}
                                cover={<img alt="example" src="data:image/png;base64," />}
                            >
                                <Meta title="精益生产" description="详细信息" />
                            </Card>
                    </List.Item>
                    )}
                />
                </TabPane>
                <TabPane tab="项目申报" key="3">
                <List
                    grid={{ gutter: 16, column: 4 }}
                    dataSource={[1,2,3,4,5,6]}
                    renderItem={item => (
                    <List.Item>
                          <Card
                                hoverable
                                style={{ width: 240 }}
                                cover={<img alt="example" src="data:image/png;base64," />}
                            >
                                <Meta title="项目申报" description="详细信息" />
                            </Card>
                    </List.Item>
                    )}
                />
                </TabPane>
            </Tabs>
             </div>
             
        </div>
    )
}
